import { useStaticQuery, graphql } from "gatsby"

import {
  Layout,
  SEO,
  FullWidthImage,
  Title,
  Reviewproduct,
  Card,
  Footer,
  Header,
  Exploreproduct,
  BannerCarousel,
  appData,
} from "../index"

const IndexPage = () => {
  const data = useStaticQuery(graphql`
    query SiteTitleQuery {
      site {
        siteMetadata {
          title
        }
      }
    }
  `)

  return (
    <Layout>
      <SEO title="Home" />
      <Header siteTitle={data.site.siteMetadata.title} {...appData.header} />
      <div className="home">
        <BannerCarousel {...appData.banner} />
        <Title {...appData.foodLabel} />
        <Reviewproduct {...appData.reviewrproduct} />
        <Card {...appData.overlayCard} />
        <Card {...appData.cardWithText} />
        <FullWidthImage {...appData.fullWithImage} />
        <Footer data={appData.footer} socialMedia="yes" />
        <Exploreproduct {...appData.explore} />
      </div>
    </Layout>
  )
}

export default IndexPage
